@font-face {
  font-family: "averto";
  font-style: normal;
  font-weight: 400;
  src: url(/themes/myproject/fonts/averto/Averto.ttf);
}
.footer__container {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.footer__container-li {
  list-style: none;
  margin-right: 50px;
}
.footer__container-li a {
  font-family: "averto", sans-serif;
  font-size: 14px;
  line-height: 28px;
  font-weight: 400;
  color: #000;
  text-decoration: none;
}

.footer__container-ul {
  padding: 0;
}

.footer__container .menu-level-0 > a {
  font-size: 20px;
  font-weight: 400;
  line-height: 28px;
  color: #000;
}

.footer__menu__logo-container {
  position: absolute;
  left: 2rem;
}
.footer__menu__logo-container .footer__menu__logo-des {
  margin-right: 120px;
}

.footer__menu__logo-des p {
  margin-right: 120px;
  color: red;
}

.footer__menu__logo-links {
  display: flex;
}
.footer__menu__logo-links a {
  color: red;
  text-decoration: none;
}